<template>
    <!-- <div class="card p-3 mb-3">
        <svg id="animation_map" width="700" height="450"></svg>
    </div> -->
<div>
    <div class="container">
      <div class="row">

        <div id="zoom-buttons">
            <button id="zoom-in">+</button>
            <button id="zoom-out">-</button>
        </div>
        <div class="row" id="controller">
          <div class="col">
            <button id="play-button">Play</button>
          </div>
          <div class="col">
            <button id="Temperature">Hide Temperature</button>
          </div>
          <div class="col">
            <button id="co2">Hide CO2</button>
          </div>
        </div>
      </div>
      <div class="row">
        <div id="vis">
          
          <div id="year_title" onmousedown="return false"></div>
        </div>
      </div>
      <div id="side-bar"></div>
    </div>
    <!-- <div id = 'test'>
    </div> -->
</div>

</template>
<script>
import Vue from 'vue';

import * as d3 from 'd3';
import { geoCylindricalStereographic} from 'd3-geo-projection';
import * as topojson from "topojson";

//data
// import { current_country } from '@/components/_global/args.js'

export default {
  name: 'animation_map',
  components: {

  },
  props: [],
  data() {
    return {
      size: [510, 950],
      on_click_country: 0,
      // size: [110, 200],
    }
  },
  methods: {
    
    resize(){
      var margin = { top: 50, right: 50, bottom: 0, left: 50 },
        width = this.size[1] - margin.left - margin.right;    //changed1 Qian

      var svg = d3.select("#maim_map")
        .attr("width", width + margin.left + margin.right)
        // .attr("height", height + margin.top + margin.bottom); 

      var svg1 = d3.select("#slider")          //changed3 Qian
        .attr("width", width + margin.left + margin.right);
        // .attr("height", 100);
    },
    createSVG() {
      var this_page = this;
      
      var temp = true;
      var co2_tf = true;
      var formatDateIntoYear = d3.timeFormat("%Y");
      var parseDate = d3.timeParse("%m/%d/%y");

      var startDate = new Date("1961-11-01"),
        endDate = new Date("2021-04-01");

      var margin = { top: 50, right: 50, bottom: 0, left: 50 },
        width = 950 - margin.left - margin.right,     //changed1 Qian
        height = 510 - margin.top - margin.bottom;     //changed2 Qian

      var svg = d3.select("#vis")
        .append("svg")
        .attr("id", "maim_map")
        .attr("width", width + margin.left + margin.right)
        .attr("height", height + margin.top + margin.bottom);

      var svg1 = d3.select("#vis")          //changed3 Qian
                   .append("svg")
                   .attr("id", "slider")
                   .attr("width", width + margin.left + margin.right)
                   .attr("height", 100);
                  //  .attr("transform", translate(50,0))

      var svg2 = d3.select("#side-bar")
                   .append("p")
                   .attr('transform', "translate(1000, 50)")
      
      const projection = geoCylindricalStereographic().scale(130);

      var path = d3.geoPath(projection);

      const zoom = d3.zoom()
        .scaleExtent([0.5, 10])
        .on('zoom', zoomed);

      svg.call(zoom);
      // d3.select("#circles").call(zoom);

      ////////// slider //////////

      var moving = false;
      var currentValue = 0;
      var targetValue = width;

      var playButton = d3.select("#play-button");
      var Temperature = d3.select("#Temperature");
      var co2 = d3.select("#co2");
      var timer;

      var x = d3.scaleTime()
        .domain([startDate, endDate])
        .range([0, targetValue])
        .clamp(true);

      var slider = svg1.append("g")     //changed4 Qian
        .attr("class", "slider")
        .attr("transform", "translate(" + margin.left + "," + 50 + ")");  //changed5 Qian

      slider.append("line")
        .attr("class", "track")
        .attr("x1", x.range()[0])
        .attr("x2", x.range()[1])
        .select(function () { return this.parentNode.appendChild(this.cloneNode(true)); })
        .attr("class", "track-inset")
        .select(function () { return this.parentNode.appendChild(this.cloneNode(true)); })
        .attr("class", "track-overlay")
        .call(d3.drag()
          .on("start.interrupt", function () { slider.interrupt(); })
          .on("start drag", function () {
          //   currentValue = d3.event.x;
            currentValue = d3.event.x;
            Vue.prototype.$currentYear = formatDateIntoYear(x.invert(currentValue));
            update(x.invert(currentValue));
          })
        );

      slider.insert("g", ".track-overlay")
        .attr("class", "ticks")
        .attr("transform", "translate(0," + 18 + ")")
        .selectAll("text")
        .data(x.ticks(10))
        .enter()
        .append("text")
        .attr("x", x)
        .attr("y", 10)
        .attr("text-anchor", "middle")
        .text(function (d) { return formatDateIntoYear(d); });

      var handle = slider.insert("circle", ".track-overlay")
        .attr("class", "handle")
        .attr("r", 9);

      var label = slider.append("text")
        .attr("class", "label")
        .attr("text-anchor", "middle")
        .text(formatDateIntoYear(startDate))
        .attr("transform", "translate(0," + (-25) + ")")

      var year_title = document.getElementById("year_title");
      year_title.innerHTML = formatDateIntoYear(startDate);


      ////////// map //////////

      var us;
      var temp_data;
      var co2_data;
      var co2_data1;

      var promises = [];
      promises.push(d3.json('countries-110m.json'));
      promises.push(d3.csv('temp_1961_2021.csv'));
      promises.push(d3.csv('co2_data.csv'));
      // console.log(zoom.translate());

      Promise.all(promises).then(function (values) {  //see https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Promise/all
          us = values[0];
          temp_data = values[1];
          co2_data = values[2];
          update(startDate);

      });   
      

      playButton
        .on("click", function () {
          var button = d3.select("#play-button");
            if (button.text() == "Pause") {
              moving = false;
              clearInterval(timer);
              button.text("Play");
            } 
            else {
              moving = true;
              timer = setInterval(step, 50);
              button.text("Pause");
            }
            // console.log("Slider moving: " + moving);
        });


      var button_temp =  d3.select("#Temperature");
      var button_temp_co2 =  d3.select("#co2");
      var original_color = [];
      var original_co2 = [];
      var original_year;
      var original_year_co2;


      Temperature
      .on("click", function () {
        if (button_temp.text() == "Hide Temperature") {
          hide_temp();
          } 
        else {
          show_temp();  
    }});

    co2
      .on("click", function () {
        if (button_temp_co2.text() == "Hide CO2") {
          hide_co2();
          } 
        else {
          show_co2();  
    }});

    function hide_temp(){
      var divsToHide = document.getElementsByClassName("color");  //divsToHide is an array
      for(var i = 0; i < divsToHide.length; i++){
        original_color.push(divsToHide[i].getAttribute("fill"));
        divsToHide[i].setAttribute("fill","#d3d3d3");
        original_year = currentValue;
        button_temp.text("Show Temperature");
        temp = false;
      }
      if(co2_tf){
        var all_co2 = document.getElementsByClassName("co2_class");  //divsToHide is an array
        for (var i = 0; i < all_co2.length; i++) {
          all_co2[i].setAttribute("fill-opacity", 0.9);
        }
      }
    }

  function legend({
    color_legend,
    title,
    tickSize = 6,
    width = 320,
    height = 44 + tickSize,
    marginTop = 18,
    marginRight = 0,
    marginBottom = 16 + tickSize,
    marginLeft = 0,
    ticks = width / 64,
    tickFormat,
    tickValues
  } = {}) {

    const svg = d3.create("svg")
      .attr("width", width)
      .attr("height", height)
      .attr("viewBox", [0, 0, width, height])
      .style("overflow", "visible")
      .style("display", "block");

    let x;

    //continuous
    if (color_legend.interpolator) {
      x = Object.assign(color_legend.copy()
        .interpolator(d3.interpolateRound(marginLeft, width - marginRight)),
        { range() { return [marginLeft, width - marginRight]; } });

      svg.append("image")
        .attr("x", marginLeft)
        .attr("y", marginTop)
        .attr("width", width - marginLeft - marginRight)
        .attr("height", height - marginTop - marginBottom)
        .attr("preserveAspectRatio", "none")
        .attr("xlink:href", ramp(color_legend.interpolator()).toDataURL());

      //scaleSequentialQuantile doesnt implement ticks or tickFormat.
      if (!x.ticks) {
        if (tickValues === undefined) {
          const n = Math.round(ticks + 1);
          tickValues = d3.range(n).map(i => d3.quantile(color_legend.domain(), i / (n - 1)));
        }
        if (typeof tickFormat !== "function") {
          tickFormat = d3.format(tickFormat === undefined ? ",f" : tickFormat);
        }
      }
    }

    //discrete
    else if (color_legend.invertExtent) {
      const thresholds
        = color_legend.thresholds ? color_legend.thresholds() // scaleQuantize
          : color_legend.quantiles ? color_legend.quantiles() // scaleQuantile
            : color_legend.domain(); // scaleThreshold

      const thresholdFormat
        = tickFormat === undefined ? d => d
          : typeof tickFormat === "string" ? d3.format(tickFormat)
            : tickFormat;

      x = d3.scaleLinear()
        .domain([-1, color_legend.range().length - 1])
        .rangeRound([marginLeft, width - marginRight]);

      svg.append("g")
        .selectAll("rect")
        .data(color_legend.range())
        .join("rect")
        .attr("x", (d, i) => x(i - 1))
        .attr("y", marginTop)
        .attr("width", (d, i) => x(i) - x(i - 1))
        .attr("height", height - marginTop - marginBottom)
        .attr("fill", d => d);

      tickValues = d3.range(thresholds.length);
      tickFormat = i => thresholdFormat(thresholds[i], i);
    }

    svg.append("g")
      .attr("transform", `translate(0, ${height - marginBottom})`)
      .call(d3.axisBottom(x)
        .ticks(ticks, typeof tickFormat === "string" ? tickFormat : undefined)
        .tickFormat(typeof tickFormat === "function" ? tickFormat : undefined)
        .tickSize(tickSize)
        .tickValues(tickValues))
      .call(g => g.selectAll(".tick line").attr("y1", marginTop + marginBottom - height))
      .call(g => g.select(".domain").remove())
      .call(g => g.append("text")
        .attr("y", marginTop + marginBottom - height - 6)
        .attr("fill", "currentColor")
        .attr("text-anchor", "start")
        .attr("font-weight", "bold")
        .text(title));

    return svg.node();
  }

    function show_temp(){
      temp = true;
      button_temp.text("Hide Temperature");
      if (currentValue == original_year){
        // change color of temp
        var divsToHide = document.getElementsByClassName("color");  //divsToHide is an array
        for(var i = 0; i < divsToHide.length; i++){
          // original_color.push(divsToHide[i].getAttribute("fill"));
          divsToHide[i].setAttribute("fill",original_color[i]);
          if(co2_tf){
            divsToHide[i].setAttribute("fill-opacity", 0.8);
          }
          else{
            divsToHide[i].setAttribute("fill-opacity", 1.0);
          }
        }
        // update opacity of all_co2
        if (co2_tf) {
          var all_co2 = document.getElementsByClassName("co2_class");  //divsToHide is an array
          for (var i = 0; i < all_co2.length; i++) {
            all_co2[i].setAttribute("fill-opacity", 0.6);
            all_co2[i].setAttribute("fill", "#ffe3e0");
          }
        }
      }
      else{
        update(x.invert(currentValue));
      }
    }

    function hide_co2(){
      var divsToHide = document.getElementsByClassName("co2_class");  //divsToHide is an array
      for(var i = 0; i < divsToHide.length; i++){
        original_co2.push(divsToHide[i].getAttribute("fill-opacity"));
        divsToHide[i].setAttribute("fill-opacity",0);
        divsToHide[i].setAttribute("stroke-width", 0);
      }
      original_year_co2 = currentValue;
      button_temp_co2.text("Show CO2");
      co2_tf = false;
    }

    function show_co2(){
      co2_tf = true;
      button_temp_co2.text("Hide CO2");
      if (currentValue == original_year_co2){
        var divsToHide = document.getElementsByClassName("co2_class");  //divsToHide is an array
        for(var i = 0; i < divsToHide.length; i++){
          // divsToHide[i].setAttribute("fill-opacity",original_co2[i]);
          if (temp) {
            divsToHide[i].setAttribute("fill-opacity", 0.6);
            divsToHide[i].setAttribute("fill", "#ffe3e0");
          }
          else {
            divsToHide[i].setAttribute("fill-opacity", 0.9);
            divsToHide[i].setAttribute("fill", "#f7a399");
          }
          divsToHide[i].setAttribute("stroke-width", 2);
        }
      }
      else{
        update(x.invert(currentValue));
      }

    }

    function step() {
      Vue.prototype.$currentYear = formatDateIntoYear(x.invert(currentValue));
      update(x.invert(currentValue));
      currentValue = currentValue + (targetValue / 151);
      if (currentValue > targetValue) {
        moving = false;
        currentValue = 0;
        this_page.$currentYear = formatDateIntoYear(x.invert(currentValue));
        clearInterval(timer);
        // timer = 0;
        playButton.text("Play");
      }
    }

    function drawPlot(us, data) {
      var states = new Map(us.objects.land.geometries.map(d => [d.id, d.properties]));

      var format = d => `${d}%`
      // d3.schemeSpectral[11]
      // 
      var color = d3.scaleQuantize([-2, 1.0], ['#df2935', '#e43d2d', '#e95124', '#ee661c', '#f37a14', 
      '#f9900b', '#fea402', '#f2ae04', '#dfb20a', '#cbb811', '#b9bd17', '#a6c11d', '#94c623', 
      '#80c334', '#6fb84c', '#5dad65', '#4ba17e', '#389599', '#268ab2', '#1c80c2', '#2977ba',
       '#376eb2', '#4465aa', '#515da2', '#5d549b', '#6a4c93']);
       // Create a Quantize color scale based on d3.schemeBlues (9 shades of Blue) 
      const color_legend = d3.scaleQuantize([-1.0, 2.0], ['#6a4c93','#376eb2','#4ba17e', 
      '#b9bd17', '#f9900b', '#df2935']);

      var mapped_data = data.map(d => [d.id, [+d.value, d.year]]);

      data = Object.assign(new Map(mapped_data));

      data.title = "Wind Electricity Generation";

      d3.select("#subplot").remove();
      d3.select("#circles").remove();
      d3.select("#subplot-mesh").remove();
          
      
      svg.append("g")
        .attr("id", "subplot")
        .attr("class","countries")
        // .attr("transform", "translate(-80,0)")    //changedn Qian
        .selectAll("path")
        .data(topojson.feature(us, us.objects.countries).features)
        // .attr("transform", "translate(-80,0)")
        .join("path")
        .attr("fill", d => {
            if(data.has(d.id) && temp){
                return color(-data.get(d.id)[0]);
            }
            else{
                // console.log(d.id)
                // return color(0);
                return "#d3d3d3";
            }
        })
        .on("click", d => {
          Vue.prototype.$current_country = d.id;
          Vue.prototype.$current_country_name = d.properties.name;
        })
        .attr("d", path)
        .attr("class", "color")
        .append("title");
      
        svg
        .append("g")
        .attr("id", "circles")
        .attr("class","circles1")
        .selectAll("myCircles")
        .data(co2_data1)
        .enter()
        .append("circle")
        .attr("cx", function (d) { return projection([+d.long, +d.lat])[0] })
        .attr("cy", function (d) { return projection([+d.long, +d.lat])[1] })
        .attr("r", function (d) { return (+d.co2_value) * 3 })
        .attr("id","smallcircle")
        .on("click", d => {
          Vue.prototype.$current_country = d.id;
          // console.log(this_page.on_click_country);
        })
        // .attr("onmouseover",setAttribute('brightness', 1.8))
        // .attr("onmouseout",this.setAttribute('brightness', 0.5))
        // .attr("r", 3 )
          .style("fill", "#fbc3bc")
          .attr("stroke", "#ef6351")
        // .attr("stroke", function (d) { if (d.co2_value > 5) { return "black" } else { return "none" } })
        .attr("stroke-width", d => {
          if (co2_tf){
            return 2;
          }
          else{
            return 0;
          }})
        .attr("fill-opacity", d => {
          if (co2_tf){
            return 0.7;
          }
          else{
            return 0;
          }
        })
        .attr("class","co2_class");

      svg.append("path")
          .attr("id", "subplot-mesh")
          .datum(topojson.mesh(us, us.objects.countries, (a, b) => a !== b))
          .attr("fill", "none")
          .attr("stroke", "#fff")
          .attr("stroke-width", 0.8)
          .attr("stroke-linejoin", "round")
          .attr("d", path);

      svg.append("g")
      .attr("transform", "translate(610,0)")
      .append(() => legend({ color_legend, title: "relative temperature", width: 260 }));
      }

    function update(h) {
      handle.attr("cx", x(h));
      label
        .attr("x", x(h))
        .text(formatDateIntoYear(h));
      year_title.innerHTML = formatDateIntoYear(h);
      h = formatDateIntoYear(h)
      // console.log(h);
      var newData = temp_data.filter(d => d.year == h)
      co2_data1 = co2_data.filter(d => d.year == h)
      drawPlot(us, newData);
    }

    function zoomed() {
      var zoomScale = d3.event.transform.k

      d3.zoomIdentity
        .scale(zoomScale)

      svg
        .selectAll('path')
        // .select("#maim_map")
        .attr('transform', d3.event.transform);
      
      svg.selectAll('circle')
        // .select("#maim_map")
        .attr('transform', d3.event.transform);

      
      // console.log(zoom.scale(), zoom.translate());
    }

    d3.select('#zoom-in').on('click', function () {
      // console.log('zoomin in')
      zoom.scaleBy(svg.transition().duration(500), 1.3);
    });

    d3.select('#zoom-out').on('click', function () {
      zoom.scaleBy(svg.transition().duration(500), 1 / 1.3);
    });
    },

    resize_map() {
      var map_container = document.getElementById("map_container");
      this.size = [map_container.offsetHeight, map_container.offsetWidth ];
      // console.log(this.on_click_country);
    }
  },
  mounted() {
    window.addEventListener("resize", this.resize_map, true);
    this.createSVG();
    // console.log("hihihihihiih");
    // console.log(this.size);
    this.$watch(
      'size',
      () => {
        this.resize();
      },
      { immediate: true }
    );
  }

}
</script>

<style>
body {
      font-family: Arial, Helvetica, sans-serif;
      font-size: 8px;
      color: #7a7a7a;
    }

    #year_title {
      position:absolute;
      background: transparent;
      display: inline-block;
      bottom: 170px;
      color:#b9b7b7;
      /* left: 10px; */
      font-size: 100pt;
    }
    #controller {
      position: relative;
      top: -80px;
      right: -30vw;
      width: 480px;
    }


    #play-button {

      background: #fb8500;
      padding-right: 26px;
      border-radius: 3px;
      border: none;
      color: #222;
      margin: 0;
      padding: 0 12px;
      width: 70px;
      cursor: pointer;
      height: 30px;
    }

    .countries :hover {
      filter: brightness(110%);
    }

    #play-button:hover {
      background-color: #E07800;
    }

    #Temperature {
    /* position: absolute; */
    /* bottom: 350px; */
    /* left: 30px; */
    background: #333;
    padding-right: 26px;
    border-radius: 3px;
    border: none;
    color: white;
    margin: 0;
    padding: 0 12px;
    width: 160px;
    cursor: pointer;
    height: 30px;
    opacity: 0.8;

  }

  #co2 {
    /* position: absolute;
    bottom: 300px;
    left: 30px; */
    background: #333;
    padding-right: 26px;
    border-radius: 3px;
    border: none;
    color: white;
    margin: 0;
    padding: 0 12px;
    width: 160px;
    cursor: pointer;
    height: 30px;
    opacity: 0.8;
    /* opacity: 0.8; */

  }

  #Temperature:hover {
    background-color: #696969;
  }

  #co2:hover {
    background-color: #696969;
  }
    .ticks {
      font-size: 10px;
    }

    .track,
    .track-inset,
    .track-overlay {
      stroke-linecap: round;
    }

    .track {
      stroke: #000;
      stroke-opacity: 0.3;
      stroke-width: 10px;
    }

    .track-inset {
      stroke: #dcdcdc;
      stroke-width: 8px;
    }

    .track-overlay {
      pointer-events: stroke;
      stroke-width: 50px;
      stroke: transparent;
      cursor: crosshair;
    }

    .handle {
      fill: #fff;
      stroke: #000;
      stroke-opacity: 0.5;
      stroke-width: 1.25px;
    }

    #zoom-buttons {
      position: absolute;
      margin-left: 10px;
      margin-top: 10px;
      padding: 5px;
      background: #fff;
    }

    #zoom-buttons button {
      background: #efefef;
      color: #231F20;
      border: 0;
      padding: 0;
      border-radius: 2px;
      width: 25px;
      height: 25px;
    }
</style>
